.weekViewWrapper {
    width: calc(100% - 24px);
    max-width: 360px;
    margin-top: 8px;
    border-radius: 8px;
    box-sizing: border-box;
    border: 2px solid #ececec;
    overflow: hidden;
}

.weekDayWrapper {
    background-color: white;
    padding: 8px 16px;
    border-bottom: 2px solid #ececec;
    font-size: 14pt;
    font-weight: 500;
    display: flex;
    align-items: center;
}
.last {
    border: none;
}

.weekDayName {
    color: #0081A7;
    margin-right: 4px;
}

.weekDayDate {

}

.weekDayProfit {
    margin-left: 4px;
    flex: 1;
    display: flex;
    justify-content: flex-end;

}

.dayDetailsWrapper {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
}
.buttonArea {

}
.addButton {
    width: 100vw;
    background-color: #00AFB9;
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 16px 0;
}

.addButton i {
    margin-right: 12px;
}
